/* eslint-disable no-nested-ternary */
/* eslint-disable camelcase */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push, goBack } from 'react-router-redux';
import 'react-responsive-carousel/lib/styles/carousel.min.css';

import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { Carousel } from 'react-responsive-carousel';
import Slide from '@material-ui/core/Slide';

import { store } from '../../../store';
// import CartDetails from './CartDetails';
import './styles.css';
import Image from '../../../common/Components/Image';
import ZoomImage from '../../../common/Components/ZoomImage';
import getBgStyle from '../../../common/Components/getBgStyle';

import {
  ADD_TO_CART, REMOVE_FROM_CART, SELECT_PRODUCT,
} from '../../../constants/actionTypes';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class ProductDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isCreateProductDialog: false,
      alreadyInCart: false,
      product: { images: [] },
      quantity: 1,
      sizes: [],
      isSizesAvailable: false,
      selectedSize: {},
      user: {},
    };
    this.cartItems = [];
    this.togelIsCreateProductDialog = this.togelIsCreateProductDialog.bind(this);
    this.togelIsZoomImageDialog = this.togelIsZoomImageDialog.bind(this);
    this.showCart = this.showCart.bind(this);
  }

  componentWillMount() {
    const {
      selectedProduct, cartItems, publicPageBgStyle, publicPageUser,
    } = this.props;
    if (selectedProduct) {
      if (selectedProduct.meta_data && selectedProduct.meta_data.sizes_available) {
        this.setState({ sizes: selectedProduct.meta_data.sizes_available, isSizesAvailable: selectedProduct.meta_data.sizes_available.length > 0 });
      }
      this.setState({ product: selectedProduct, isSizesAvailable: selectedProduct.sizes_available, sizes: selectedProduct.sizes });
      this.setMinMaxPrice(selectedProduct.sizes);
    }
    if (cartItems && selectedProduct) {
      this.cartItems = cartItems;
      const temp = cartItems.filter((c) => c.item.id === selectedProduct.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity, selectedSize: temp[0].selectedSize });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: '' });
      }
    }
    const { secondaryColor, fontColor } = getBgStyle(publicPageBgStyle);
    this.setState({ secondaryColor, fontColor });

    if (publicPageUser) {
      this.setState({ user: publicPageUser });
    }
    
  }

  componentWillReceiveProps(np) {
    const { user } = this.state;
    if (np.routingLocation) {
      if (np.routingLocation.query) {
        const { publicProductDetails, imageZoom } = np.routingLocation.query;
        if (publicProductDetails) {
          this.setState({ isCreateProductDialog: publicProductDetails, isZoomImageDialogOpen: imageZoom });
        } else {
          this.setState({ isCreateProductDialog: false, isZoomImageDialogOpen: false });
        }
      }
    }
    if (np.selectedProduct && np.selectedProduct !== this.state.product) {
      if (np.selectedProduct.meta_data && np.selectedProduct.meta_data.sizes_available) {
        this.setState({ sizes: np.selectedProduct.meta_data.sizes_available, isSizesAvailable: np.selectedProduct.meta_data.sizes_available.length > 0 });
      }
      this.setState({ product: np.selectedProduct, isSizesAvailable: np.selectedProduct.sizes_available, sizes: np.selectedProduct.sizes });
      this.setMinMaxPrice(np.selectedProduct.sizes);
    }

    if (np.publicPageUser && np.publicPageUser !== user) {
      this.setState({ user: np.publicPageUser });
    }
    if (np.cartItems && np.selectedProduct) {
      this.cartItems = np.cartItems;
      const temp = np.cartItems.filter((c) => c.item.id === np.selectedProduct.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity, selectedSize: temp[0].selectedSize  });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: '' });
      }
    }
    const { secondaryColor, fontColor } = getBgStyle(np.publicPageBgStyle);
    this.setState({ secondaryColor, fontColor });
  }

  setMinMaxPrice(sizes) {
    if (!sizes || sizes.length <= 0) {
      this.setState({ minPrice: 0, maxPrice: 0 });
      return;
    }
    if (!sizes[0].price) return;
    let minPrice = sizes[0].disc_price;
    let maxPrice = sizes[0].disc_price;
    sizes.forEach((p) => {
      minPrice = Math.min(p.disc_price, minPrice);
      maxPrice = Math.max(p.disc_price, maxPrice);
    });
    this.setState({ minPrice, maxPrice });
  }

  togelIsCreateProductDialog() {
    const { routingLocation, selectProduct } = this.props;
    const { query } = routingLocation;
    const { isCreateProductDialog, product } = this.state;
    if (isCreateProductDialog) {
      selectProduct({});
      store.dispatch(goBack());
    } else {
      let path = `${window.location.pathname}?`;
      query.publicProductDetails = true;
      Object.keys(query).forEach((q) => {
        path += `${q}=${query[q]}&`;
      });
      path.substring(0, path.length - 2);
      store.dispatch(push(path));
    }
  }

  togelIsZoomImageDialog() {
    const { isZoomImageDialogOpen } = this.state;

    if (isZoomImageDialogOpen) {
      store.dispatch(goBack());
    } else {
      const path = window.location.search ? `${window.location.pathname}${window.location.search}&imageZoom=true`
        : `${window.location.pathname}?imageZoom=true`;
      store.dispatch(push(path));
    }
  }

  showCart() {
    // const path = window.location.search ? `${window.location.pathname}${window.location.search}&cartDetails=true`
    //   : `${window.location.pathname}?cartDetails=true`;

    const { routingLocation } = this.props;
    let path = `${window.location.pathname}?`;
    const { query } = routingLocation;
    if (routingLocation.query.tab) {
      path += `cartDetails=true&tab=${query.tab}`;
    } else {
      path += 'cartDetails=true';
    }

    store.dispatch(push(path));
  }

  addToCart() {
    const {
      isSizesAvailable, selectedSize, product, quantity, user,
    } = this.state;
    if (isSizesAvailable && selectedSize === '') {
      this.setState({ showSizes: true });
    } else {
      const data = {
        item: product,
        quantity,
        selectedSize,
        seller: user,
      };
      this.props.addToCart(data);
      this.setState({ showSizes: false });
    }
  }

  

  modifyQuantity(change) {
    if (this.state.quantity + change > 0) this.setState({ quantity: this.state.quantity + change });
  }

  selectSize(e) {
    const { sizes } = this.state;
    this.setState({ selectedSize: sizes[e.target.value] });
  }

  renderZoomImageDialog() {
    const { isZoomImageDialogOpen, product } = this.state;

    return (
      <Dialog
        open={isZoomImageDialogOpen}
        TransitionComponent={Transition}
        keepMounted
        fullScreen
        name="zoomImageDialog"
        onClose={this.togelIsZoomImageDialog.bind(this)}
      >
        <div className="tc b f5">
          <p>
          Double click or pinch to zoom.
          </p>
        </div>

        <DialogContent>
          <div>
            {
               product && product.images && product.images.length > 0
                 ? (
                   <Carousel className="product-details-carousel" style={{ background: 'white' }}>
                     { product.images.map((img, i) => (
                       <ZoomImage key={i}>
                         <img src={img} alt="" style={{ width: 'auto', maxHeight: window.innerHeight - 200, margin: 'auto' }} overlaySrc={product.preview_images[0]} key={i} />
                       </ZoomImage>
                     ))}
                   </Carousel>
                 )
                 : null
            }

          </div>
        </DialogContent>

        <DialogActions>
          <button type="button" className="color-btn w-50" onClick={this.togelIsZoomImageDialog}> close</button>
        </DialogActions>
      </Dialog>
    );
  }

  render() {
    const {
      name, images, price, disc_price, description, estimated_delivery, preview_images, cod_available,
    } = this.state.product;
    const {
      alreadyInCart, isCreateProductDialog, sizes, showSizes, secondaryColor, fontColor, minPrice, maxPrice, isSizesAvailable, selectedSize,
    } = this.state;
    console.log(selectedSize);
    return (
      <div>
        <Dialog
          open={isCreateProductDialog}
          TransitionComponent={Transition}
          keepMounted
          fullScreen
          onClose={this.togelIsCreateProductDialog.bind(this)}
        >
          <div className="cart-details-top-bar" style={{ backgroundColor: secondaryColor, color: fontColor }}>
            <h3>{name}</h3>
            <div className="flex items-center justify-between">
              <button className="home-page-cart-div items-center ju flex relative ml1 mr1" type="button" onClick={this.showCart} style={{ color: fontColor, fontWeight: 'bold' }}>
                <ShoppingCartIcon />
                <span>
                  {`Cart(${this.cartItems.length})`}
                </span>
              </button>
              <button type="button" onClick={this.togelIsCreateProductDialog.bind(this)} style={{ color: fontColor }}>
                Close
              </button>
            </div>
          </div>
          <DialogContent>
            <div className="product-details">
              <div className="product-details-top-div">
                {
                  images && images.length > 0
                    ? (
                      <Carousel className="product-details-carousel" background="#000" onClickItem={this.togelIsZoomImageDialog.bind(this)} showThumbs={false}>
                        { images.map((img, i) => (
                          <Image src={img} alt="" overlaySrc={preview_images[0]} key={i} />
                        ))}
                      </Carousel>
                    )
                    : null
                }
                <div className="cart-details-top-right-div">
                  <h2>{name}</h2>
                  <div className="product-pricing">
                    {
                      !isSizesAvailable
                        ? (
                          disc_price === price
                            ? (
                              <b>{`Rs. ${price}`}</b>
                            )
                            : (
                              <div>
                                <b>{`Rs. ${disc_price}`}</b>
                                <strike>{` Rs. ${price}`}</strike>
                                <span className="product-details-product-sale">
                                  {`${Math.floor(((price - disc_price) / price) * 100)} %off`}
                                </span>
                              </div>
                            )
                        ) : (
                          selectedSize ? (
                            selectedSize.disc_price === selectedSize.price
                              ? (
                                <b>{`Rs. ${selectedSize.price}`}</b>
                              )
                              : (
                                <div>
                                  <b>{`Rs. ${selectedSize.disc_price}`}</b>
                                  <strike>{` Rs. ${selectedSize.price}`}</strike>
                                  <span className="product-details-product-sale">
                                    {`${Math.floor(((selectedSize.price - selectedSize.disc_price) / price) * 100)} %off`}
                                  </span>
                                </div>
                              )
                          )
                            : (
                              minPrice !== maxPrice
                                ? (
                                  <b>{`Rs. ${minPrice} ~ Rs.${maxPrice}`}</b>
                                )
                                : (
                                  <b>{`Rs.${maxPrice}`}</b>
                                )
                            )
                        )
                    }
                  </div>
                  <h4>
                    {`Estimated Delivery:${estimated_delivery}`}
                  </h4>
                  {
                    cod_available
                      ? (
                        <p className="err-txt green b">Cash on delivery available</p>
                      )
                      : null
                  }
                  <div className="product-add-to-cart-div">
                    {
                      alreadyInCart
                        ? (
                          <button type="button" className="product-add-to-cart relative" onClick={this.showCart} style={{ background: secondaryColor, color: fontColor }}>
                            View Cart
                          </button>
                        )
                        : (
                          <div>
                            <div className="flex flex-column mt1 mb1">
                              {
                                sizes && sizes.length > 0
                                && (
                                <select onChange={this.selectSize.bind(this)}>
                                  <option value={null}>Select Size</option>
                                  {
                                  sizes.map((s, i) => (
                                    <option value={i} key={i}>{s.size}</option>
                                  ))
                                  }
                                </select>
                                )
                              }
                              {
                                showSizes ? <p className="err-txt"> *Select a size </p> : null
                              }
                            </div>
                            <button
                              type="button"
                              className="product-add-to-cart"
                              onClick={this.addToCart.bind(this)}
                              style={{ background: secondaryColor, color: fontColor }}
                            >
                              Add To Cart
                            </button>
                          </div>
                        )
                    }
                    {
                      alreadyInCart
                        ? (

                          <div className="f6 go-to-cart relative">
                          Item already in cart.
                          </div>
                        )
                        : null
                    }
                  </div>
                  <div style={{ overflowWrap: 'anywhere' }}>
                    <h4> Description : </h4>
                    <p>{description}</p>
                  </div>
                </div>
              </div>
            </div>
          </DialogContent>
          {
            this.renderZoomImageDialog()
          }
        </Dialog>
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,
  routingLocation: state.router.location,
  homeBgStyle: state.AdminPageReducers.homeBgStyle,
  publicPageBgStyle: state.HomePageReducers.publicPageBgStyle,
  selectedProduct: state.HomePageReducers.selectedProduct,
  publicPageUser: state.HomePageReducers.publicPageUser,

});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
  selectProduct: (product) => dispatch({ type: SELECT_PRODUCT, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ProductDetails);
